import React from "react";
import { HeadingType } from "../../general/Heading";
import { FontColor, FontWeight, FontSize } from "../../../data/style/variables";
import {
  Wrapper,
  Heading,
  Background,
  Top,
  SocialWrapper,
  SocialNetworks,
  SocialNetworkLink,
  Sitemap,
  SitemapGroup,
  SitemapGroupTag,
  SitemapGroupLinks,
  SitemapLink,
  Bottom,
  Logo,
  Disclaimer
} from "./styled";
import Icon from "../../general/Icon";

type SocialNetwork = {
  uuid: string;
  icon: React.ReactNode;
  link: ILink;
};

type SitemapGroup = {
  uuid: string;
  tag: String;
  links: (ILink & { uuid: string })[];
};

type FooterProps = {
  inverted?: boolean;
};

const sitemap: SitemapGroup[] = [
  {
    uuid: "1",
    tag: "Blog",
    links: [
      {
        uuid: "1",
        children: "Blog",
        title: "Blog",
        href: "/article"
      }
    ]
  },
  {
    uuid: "2",
    tag: "Quickscan",
    links: [
      {
        uuid: "1",
        children: "Doe de Quickscan",
        title: "Doe de Quickscan",
        href: "/quickscan"
      }
    ]
  }
];

const socialNetworks: SocialNetwork[] = [
  {
    uuid: "LinkedIn",
    icon: <Icon.LinkedIn />,
    link: {
      href: "https://www.linkedin.com/company/studata/",
      target: "_blank"
    }
  },

  {
    uuid: "Email",
    icon: <Icon.Email />,
    link: {
      href: "mailto:contact@datascienceplatform.nl",
      target: "_blank"
    }
  }
];

const Footer: React.FC<FooterProps> = ({ inverted = false }) => {
  const renderedSocialNetworks = (
    <SocialNetworks>
      {socialNetworks.map(socialNetwork => (
        <SocialNetworkLink
          {...socialNetwork.link}
          key={socialNetwork.uuid}
          external
        >
          {socialNetwork.icon}
        </SocialNetworkLink>
      ))}
    </SocialNetworks>
  );

  return (
    <Background inverted={inverted}>
      <Wrapper>
        <Top>
          <SocialWrapper>
            <Heading
              headingType={HeadingType.Secondary}
              fontWeight={FontWeight.Black}
            >
              Vind ons op
            </Heading>

            {renderedSocialNetworks}
          </SocialWrapper>

          <Sitemap>
            {sitemap.map(({ uuid, tag, links }) => (
              <SitemapGroup key={uuid}>
                <SitemapGroupTag
                  fontColor={
                    inverted ? FontColor.Primary : FontColor.SecondaryInverted
                  }
                  fontSize={FontSize.Large}
                  fontWeight={FontWeight.Black}
                >
                  {tag}
                </SitemapGroupTag>

                <SitemapGroupLinks>
                  {links.map(link => (
                    <SitemapLink
                      {...link}
                      key={link.uuid}
                      fontSize={FontSize.Large}
                      fontWeight={FontWeight.Bold}
                      serif
                    >
                      {link.children}
                    </SitemapLink>
                  ))}
                </SitemapGroupLinks>
              </SitemapGroup>
            ))}
          </Sitemap>
        </Top>

        <Bottom>
          <Logo showName />

          <Disclaimer
            fontColor={
              inverted ? FontColor.Primary : FontColor.SecondaryInverted
            }
            serif
          >
            Data Science Platform - Copyright {new Date().getFullYear()}
          </Disclaimer>
        </Bottom>
      </Wrapper>
    </Background>
  );
};

export default Footer;
